.trending{

    display: flex;
    animation: marquee 10s linear infinite;
    margin-top: 2rem;
}
.trending p {
    display: flex;
    place-items: center;
    padding: 1rem;
    border: 1px solid grey;
    margin-right: 1rem;
}
@keyframes marquee {
    0% { transform: translateX(-50%); }
    100% { transform: translateX(-150%); }
}