.modale-background{
    width: 100%;
    position: fixed;
    top: 0;
    bottom: 0;
    background-color: #404449;
    display: flex;
    z-index: 100;
    padding: 1rem;
}
.modale{
    background-color: #ffe910;
    margin:auto;
    min-height: 50vh;
    max-width: 50rem;
    padding: 2rem;
    line-height: 2;
    position: relative;
}
.modale button{
    position: absolute;
    top: 1rem;
    right: 1rem;
}
.formulaire-comments{
    margin-top: 2rem;
}
.container-comments form > *{
    margin-bottom: 0.3rem;
}