.formulaire{
    padding-top: 3rem;
    padding-bottom: 3rem;
    background-color: #ffe910;
    display: flex;
    overflow-x: hidden;
}

form{
    width: 100%;
    max-width: 60rem;
}
form > *{
    font-size: 1.6rem;
    display: block;
    margin-bottom: 2rem;
    font-weight: 500;
}
.label, input, textarea{
    width: 100%;

}
input, textarea{
    height: 4rem;

}
.button-submit{
    width: max-content;
    padding: 2rem;
    height: auto;
    margin-bottom: 0rem;
    background: #404449;
    color: white;
    font-weight: 500;
    border-radius: 5px;
    border: 1px solid #404449;
}
.button-submit:hover,
.button-submit:focus{
    background: white;
    color: #404449;
}